import React, { FC } from 'react';
import makeStyles from '@material-ui/core/styles/makeStyles';
import clsx from 'clsx'

interface Props {
  open: boolean;
  card: string;
  onClick?: () => void;
}

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    background: theme.palette.background.paper,
    width: theme.spacing(6),
    height: theme.spacing(8),
    borderStyle: 'solid',
    borderRadius: theme.spacing(1),
    borderWidth: 2,
    margin: `${theme.spacing(1)}px ${theme.spacing(2)}px`
  },
  closed: {
    borderColor: ' #4a235a ',
    backgroundColor: '#7d3c98',
    color: '#fff '
  },
  red: {
    borderColor: '#F00',
    color: '#F00'
  },
  black: {
    borderColor: '#000',
    color: '#000'
  }
}));

/**
 * Card components
 *
 * @param props
 */
const CardComponent: FC<Props> = (props) => {
  const { open, card, onClick } = props;
  const classes = useStyles(props);

  const [suit, value] = card.split('', 2);

  let Icon = <>&#8466;</>;

  switch (suit) {
    case 'c':
      Icon = <>&clubs;</>;
      break;
    case 'd':
      Icon = <>&diams;</>;
      break;
    case 'h':
      Icon = <>&hearts;</>;
      break;
    case 's':
      Icon = <>&spades;</>;
      break;
    default:
      Icon = <>?</>;
  }

  const red = suit === 'h' || suit === 'd';

  return (
    <div
      className={clsx(classes.root, open && red && classes.red, open && !red && classes.black, !open && classes.closed)}
      onClick={onClick}
    >
      {
        open ?
          (
            <span>{Icon} {value}</span>
          ) : (
            <span>&#8466;</span>
          )
      }
    </div>
  )
}

export default CardComponent;
