import React, { FC } from 'react';
import { BrowserRouter as Router, Route, Switch, useRouteMatch } from 'react-router-dom';
import useClientState from '../hooks/use-client-state.hook';
import ClientBus from './client/bus.page';
import ClientJoin from './client/join.page';
import ClientLobby from './client/lobby.page';
import ClientPyramid from './client/pyramid.page';
import ClientSharing from './client/sharing.page';


const ClientPage: FC = (props) => {

  const match = useRouteMatch();

  const [state, setState] = useClientState();

  return (
    <Router>
      <Switch>
        <Route path={`${match.url}/:room/lobby`}>
          <ClientLobby state={state} setState={setState} />
        </Route>
        <Route path={`${match.url}/:room/sharing`}>
          <ClientSharing state={state} setState={setState} />
        </Route>
        <Route path={`${match.url}/:room/pyramid`}>
          <ClientPyramid state={state} setState={setState} />
        </Route>
        <Route path={`${match.url}/:room/bus`}>
          <ClientBus state={state} setState={setState} />
        </Route>
        <Route path={`${match.url}`} exact>
          <ClientJoin state={state} setState={setState} />
        </Route>
      </Switch>
    </Router >
  )
}

export default ClientPage;
