import { Button, Card, CardActions, CardContent, Grid, makeStyles, TextField, Typography } from '@material-ui/core';
import React, { Dispatch, FC, SetStateAction, useState } from 'react';
import { useHistory, useRouteMatch } from 'react-router-dom';
import ClientState from '../../shared/client-state';

type Props = {
  state: ClientState;
  setState: Dispatch<SetStateAction<ClientState>>;
}
const useStyles = makeStyles(theme => ({
  root: {
    position: 'absolute',
    top: 0,
    bottom: 0
  },
  input: {
    marginBottom: theme.spacing(2)
  },
  card: {
    margin: '0 auto',
    maxWidth: '95%'
  }
}));

/**
 * Form to join a game
 * @param props
 */
const ClientJoin: FC<Props> = (props) => {

  const { setState } = props;
  const classes = useStyles(props);
  const match = useRouteMatch();
  const history = useHistory();

  const [room, setRoom] = useState('');
  const [name, setName] = useState('');

  const cantJoin = room === '' && name === '';

  function handleJoin() {
    // TODO check and join the websocket
    // Get an ID back (FIXME: remove name)
    setState({ name: name, room: room, id: name });
    history.push(`${match.url}/${room}/lobby`);
  }

  return (
    <Grid
      className={classes.root}
      container
      alignItems="center"
      justify="center"
    >
      <Grid item>
        <Card className={classes.card}>
          <CardContent>
            <Typography gutterBottom variant="h5">
              Doe mee met een spel
            </Typography>
            <TextField
              className={classes.input}
              label="Code"
              value={room}
              onChange={evt => setRoom(evt.target.value)}
              fullWidth
            />
            <TextField
              className={classes.input}
              label="Schuilnaam"
              value={name}
              onChange={evt => setName(evt.target.value)}
              fullWidth
            />
          </CardContent>
          <CardActions>
            <Button
              color="primary"
              variant="contained"
              disabled={cantJoin}
              onClick={handleJoin}
            >
              begin
            </Button>
          </CardActions>
        </Card>
      </Grid>
    </Grid>
  )
}

export default ClientJoin;
