import { Card, CardContent, Grid, makeStyles, Typography } from '@material-ui/core';
import React, { Dispatch, FC, SetStateAction } from 'react';
import ClientState from '../../shared/client-state';

type Props = {
  state: ClientState;
  setState: Dispatch<SetStateAction<ClientState>>;
}


const useStyles = makeStyles(theme => ({
  root: {
    position: 'absolute',
    top: 0,
    bottom: 0
  },
  card: {
    margin: '0 auto',
    maxWidth: '95%'
  }
}));


/**
 * Page for waiting for everyone to be ready
 *
 * @param props
 */
const ClientLobby: FC<Props> = (props) => {

  const { state } = props;
  const classes = useStyles();

  return (
    <Grid
      className={classes.root}
      container
      alignItems="center"
      justify="center"
    >
      <Grid item>
        <Card className={classes.card}>
          <CardContent>
            <Typography variant="body1">
              Welkom {state.name}. Pak een drankje en wacht tot het spel wordt
              gestart.
            </Typography>
          </CardContent>
        </Card>
      </Grid>
    </Grid>
  )
}

export default ClientLobby;
