import { Dispatch, SetStateAction, useEffect, useState } from "react";
import ClientState from "../shared/client-state";

const LOCAL_KEY = 'kbus-data';

function parseLocalData(data: string): ClientState | null {
  const parsed: object = JSON.parse(data);
  if ('name' in parsed && 'room' in parsed && 'id' in parsed) {
    return parsed as ClientState
  }

  return null;
}

function useClientState(): [ClientState, Dispatch<SetStateAction<ClientState>>] {

  const [state, setState] = useState<ClientState>(() => {

    // Check local storage for state
    const item = window.localStorage.getItem(LOCAL_KEY);
    if (item) {
      const parsed = parseLocalData(item);
      if (parsed) {
        return parsed as ClientState;
      }
    }

    return {
      name: '',
      room: null,
      id: null
    }
  });

  useEffect(() => {
    const data = JSON.stringify(state);
    window.localStorage.setItem(LOCAL_KEY, data);
  }, [state])

  return [state, setState];

}

export default useClientState;
